<template>
    <div>
        <add-asset-template>
            <template v-slot:form>
                <v-form ref="form" v-model="valid" lazy-validation>
                    <v-text-field v-model="bathroom.name" :rules="nameRules" label="Name" required></v-text-field>
                    <v-text-field
                        v-model="bathroom.capacity"
                        :rules="capacityRules"
                        label="Capacity"
                        required
                    ></v-text-field>
                    <v-select
                        v-model="bathroom.gender"
                        :items="genders"
                        label="Gender"
                        :rules="genderRules"
                        required
                    ></v-select>
                </v-form>
            </template>
            <template v-slot:buttons>
                <v-btn :disabled="!valid" color="primary" @click="save">Save</v-btn>
            </template>
        </add-asset-template>
    </div>
</template>

<script>
import AddAssetTemplate from './AddAssetTemplate.vue'
import { mapState, mapActions, mapMutations } from 'vuex'

export default {
    props: {
        floorId: {
            type: String
        }
    },
    data: () => ({
        genders: ['M', 'F'],
        valid: true,
        bathroom: {
            name: undefined,
            gender: undefined,
            capacity: null,
            floorId: undefined,
            shape: undefined
        },
        nameRules: [
            v => !!v || 'Name is required',
            v => (v && v.length >= 3) || 'Name must be more than 3 characters',
            v => (v && v.length <= 100) || 'Name must be less than 100 characters'
        ],
        capacityRules: [
            v => !!v || 'Capacity is required',
            v => (v && v >= 1) || 'Capacity must be more at least 1 person',
            v => (v && v <= 10) || 'Capacity must be less than 10 persons'
        ],
        genderRules: [
            v => !!v || 'Gender is required'
        ]
    }),
    components: {
        'add-asset-template': AddAssetTemplate
    },
    computed: {
        ...mapState('assetInteraction', ['assetSelected', 'drawnShape'])
    },
    methods: {
        ...mapActions('bathrooms', ['postBathroom']),
        ...mapMutations('assetInteraction', ['setViewingMode']),
        save() {
            if (this.$refs.form.validate()) {
                this.bathroom.floorId = this.floorId
                this.bathroom.shape = JSON.stringify(this.drawnShape)
                this.postBathroom(this.bathroom).then(() => {
                    this.setViewingMode()
                })
            }
        }
    }
}
</script>

