<template>
    <v-layout class="stayToTheRight">
        <v-card class="cardStyle">
            <v-card-title class="cardTitleStyle">{{addAssetName}}</v-card-title>
            <v-container column justify-center align-center class="inputForm">
                <slot name="form"></slot>
            </v-container>
            <v-layout row justify-center align-center>
                <v-dialog v-model="dialog" persistent max-width="290">
                    <template v-slot:activator="{ on }">
                        <v-btn flat color="primary" dark v-on="on">Cancel</v-btn>
                    </template>
                    <v-card>
                        <v-card-title class="headline">Dismiss changes</v-card-title>
                        <v-card-text>If you dismiss changes you will lose all the data that was not saved.</v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="primary" flat @click="dialog = false">No</v-btn>
                            <v-btn color="primary" flat @click="cancelDrawing()">Dismiss changes</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
                <slot name="buttons"></slot>
            </v-layout>
        </v-card>
    </v-layout>
</template>

<script>
import { mapState, mapActions, mapMutations } from 'vuex'

export default {
    data: () => ({
        dialog: false
    }),
    computed: {
        ...mapState('assetInteraction', ['assetSelected']),
        addAssetName() {
            return 'Add ' + this.assetSelected.name.toLowerCase()
        }
    },
    methods: {
        ...mapActions('assetInteraction', ['setReloadMap']),
        ...mapMutations('assetInteraction', ['setViewingMode']),
        cancelDrawing() {
            this.setViewingMode()
            this.setReloadMap(true)
        }
    }
}
</script>

<style lang="scss" scoped>
@import 'src/stylesheets/1-setttings/settings.colors.scss';

.stayToTheRight {
    position: absolute;
    right: 25px;
}
.cardStyle {
    width: 260px;
}
.cardTitleStyle {
    background-color: $orange;
    color: white;
    font-size: 18px;
    justify-content: center;
}
.inputForm {
    padding: 15px;
    padding-top: 5px;
}
</style>