<template>
    <div>
        <add-asset-template>
            <template v-slot:form>
                <v-form ref="form" v-model="valid" lazy-validation>
                    <v-text-field v-model="kitchen.name" :rules="nameRules" label="Name" required></v-text-field>
                    <v-text-field
                        v-model="kitchen.capacity"
                        :rules="capacityRules"
                        label="Capacity"
                        required
                    ></v-text-field>
                    <v-switch v-model="kitchen.hasRecycleBins" label="RecycleBins" color="primary" hide-details></v-switch>
                    <v-textarea
                        v-model="kitchen.description"
                        label="Description"
                        :rules="descriptionRules"
                        counter="500"
                    ></v-textarea>
                </v-form>
            </template>
            <template v-slot:buttons>
                <v-btn :disabled="!valid" color="primary" @click="save">Save</v-btn>
            </template>
        </add-asset-template>
    </div>
</template>

<script>
import AddAssetTemplate from './AddAssetTemplate.vue'
import { mapState, mapActions, mapMutations } from 'vuex'

export default {
    props: {
        floorId: {
            type: String
        }
    },
    data: () => ({
        valid: true,
        kitchen: {
            name: undefined,
            floorId: undefined,
            shape: undefined,
            capacity: null,
            hasRecycleBins: false,
            description: undefined
        },
        nameRules: [
            v => !!v || 'Name is required',
            v => (v && v.length >= 3) || 'Name must be more than 3 characters',
            v => (v && v.length <= 100) || 'Name must be less than 100 characters'
        ],
        capacityRules: [
            v => !!v || 'Capacity is required',
            v => (v && v >= 3) || 'Capacity must be more than 2 persons',
            v => (v && v <= 100) || 'Capacity must be less than 100 persons'
        ],
        descriptionRules: [
            v => !!v || 'Description is required',
            v => (v && v.length >= 3) || 'Description must be longer than 2 characters',
            v => (v && v.length <= 500) || 'Description must be less than 500 characters'
        ]
    }),
    components: {
        'add-asset-template': AddAssetTemplate
    },
    computed: {
        ...mapState('assetInteraction', ['assetSelected', 'drawnShape'])
    },
    methods: {
        ...mapActions('kitchens', ['postKitchen']),
        ...mapMutations('assetInteraction', ['setViewingMode']),
        save() {
            if (this.$refs.form.validate()) {
                this.kitchen.floorId = this.floorId
                this.kitchen.shape = JSON.stringify(this.drawnShape)
                this.postKitchen(this.kitchen).then(() => {
                    this.setViewingMode()
                })
            }
        }
    }
}
</script>

