<template>
    <v-layout class="stayToTheRight">
        <v-card>
            <v-card-title class="cardTitleStyle">Start drawing</v-card-title>
            <v-card-title class="cardTitle">You can now start drawing on the map</v-card-title>
            <v-card-actions>
                <v-btn color="primary" flat @click="stopDrawing()">Cancel</v-btn>
            </v-card-actions>
        </v-card>
    </v-layout>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
    methods: {
        ...mapMutations('assetInteraction', ['setViewingMode']),
        stopDrawing() {
            this.setViewingMode()
        }
    }
}
</script>

