<template>
    <v-card id="treeviewCard">
        <v-treeview :open="open" :items="assets" :active.sync="active" activatable item-key="name" open-on-click class="assetsTreeview">
            <template v-slot:prepend="{ item }" @click="selectAsset()" id="assetTitle">
                <div v-if="item.color" id="MapRepresentation" :style="'background-color:' + item.color"></div>
            </template>
        </v-treeview>
    </v-card>
</template>

<script>
import { meetingRoom, KITCHEN, BATHROOM, RECREATION_ROOM, RECEPTION, EXIT, PRINTER, DESK, DEFAULT } from '@/store/commonSpaces.entityTypes.js'
import { mapState, mapActions, mapMutations } from 'vuex'
export default {
    name: 'AssetsTreeview',
    data: () => ({
        open: ['Office assets'],
        active: [],
        assets: [
            {
                name: 'Office assets',
                children: [meetingRoom, KITCHEN, BATHROOM, RECREATION_ROOM, RECEPTION, EXIT, PRINTER, DESK],
            },
            {
                name: 'Parking',
                children: [
                    {
                        name: 'Car',
                        color: '#0f39d14D',
                        type: 'Polygon',
                    },
                    {
                        name: 'Motorcycle',
                        color: '#e214d54D',
                        type: 'Polygon',
                    },
                    {
                        name: 'Bike',
                        color: '#4dd1104D',
                        type: 'Polygon',
                    },
                ],
            },
        ],
    }),
    computed: {
        ...mapState('assetInteraction', ['mapStates', 'assetSelected', 'drawnShape']),
        drawing() {
            return this.mapStates.drawing
        },
    },
    watch: {
        active() {
            this.selectAsset()
        },
        mapStates() {
            if ((this.mapStates.viewing || this.mapStates.editing) && this.active.length) {
                this.active = []
            }
        },
    },
    methods: {
        ...mapActions('assetInteraction', ['setAssetSelected']),
        ...mapMutations('assetInteraction', ['setDrawingMode', 'setViewingMode']),
        selectAsset: function () {
            if (this.active.length) {
                const name = this.active[0]
                for (const asset of this.assets) {
                    const assetExists = asset.children.find((e) => e.name === name)
                    if (assetExists) {
                        this.setAssetSelected(assetExists)
                        this.setDrawingMode()
                    }
                }
            } else {
                this.setAssetSelected(DEFAULT)
                this.setViewingMode()
            }
        },
    },
}
</script>

<style>
#MapRepresentation {
    width: 35px;
    height: 25px;

    margin-right: 5px;
}
#treeviewCard {
    width: 250;
    min-width: 225px;
}

.assetsTreeview .v-treeview-node--leaf {
    margin: 0 20px;
}
.assetsTreeview .v-treeview-node__content {
    padding: 0 5px;
}
.assetsTreeview .theme--light.v-treeview--hoverable .v-treeview-node__root:hover,
.theme--light.v-treeview .v-treeview-node--active {
    border-right: 2px solid #db4326;
    background: rgb(245, 245, 245);
}

.assetsTreeview #assetTitle {
    margin-left: -20px;
}
</style>
