<template>
    <div>
        <add-asset-template>
            <template v-slot:form>
                <v-form ref="form" v-model="valid" lazy-validation>
                    <v-text-field
                        v-model="desk.assetNumber"
                        :rules="assetNumberRules"
                        label="Asset number"
                        required
                    ></v-text-field>
                    <v-select
                        v-model="desk.type"
                        :items="deskTypes"
                        label="Type"
                        :rules="typeRules"
                    ></v-select>
                </v-form>
            </template>
            <template v-slot:buttons>
                <v-btn :disabled="!valid" color="primary" @click="save">Save</v-btn>
            </template>
        </add-asset-template>
    </div>
</template>

<script>
import AddAssetTemplate from './AddAssetTemplate.vue'
import { mapState, mapActions, mapMutations} from 'vuex'

export default {
    props: {
        floorId: {
            type: String
        }
    },
    data: () => ({
        valid: true,
        deskTypes: ['Standard', 'Standing'],
        desk: {
            assetNumber: undefined,
            type: undefined,
            floorId: undefined,
            shape: undefined
        },
        assetNumberRules: [v => !!v || 'Asset number is required', v =>(v && v.length == 8) || 'Asset number mush have 8 characters'],
        typeRules: [v => !!v || 'Type is required']
    }),
    components: {
        'add-asset-template': AddAssetTemplate
    },
    computed: {
        ...mapState('assetInteraction', ['assetSelected', 'drawnShape'])
    },
    methods: {
        ...mapActions('desks', ['postDesk']),
        ...mapMutations('assetInteraction', ['setViewingMode']),
        save() {
            if (this.$refs.form.validate()) {
                this.desk.floorId = this.floorId
                this.desk.shape = JSON.stringify(this.drawnShape)
                this.postDesk(this.desk).then(() => {
                    this.setViewingMode()
                })
            }
        }
    }
}
</script>

