<template>
  <div>
    <add-asset-template>
      <template v-slot:form>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field v-model="printer.name" :rules="nameRules" label="Name" required></v-text-field>
          <v-text-field v-model="printer.link" :rules="linkRules" label="Link" required></v-text-field>
          <v-select
            v-model="printer.type"
            :items="printerTypes"
            label="Printer type"
            :rules="typeRules"
          ></v-select>
          <v-switch v-model="printer.hasScanner" label="Scanner" color="primary" hide-details></v-switch>
          <v-switch
            v-model="printer.hasLargeSizeSupport"
            label="Large size support"
            color="primary"
            hide-details
          ></v-switch>
        </v-form>
      </template>
      <template v-slot:buttons>
        <v-btn :disabled="!valid" color="primary" @click="save">Save</v-btn>
      </template>
    </add-asset-template>
  </div>
</template>

<script>
import AddAssetTemplate from './AddAssetTemplate.vue'
import { mapState, mapActions, mapMutations } from 'vuex'

export default {
    props: {
        floorId: {
            type: String
        }
    },
    data: () => ({
        valid: true,
        printerTypes: ['Color', 'Black and white'],
        printer: {
            name: undefined,
            type: undefined,
            hasScanner: false,
            hasLargeSizeSupport: false,
            link: undefined,
            floorId: undefined,
            shape: undefined
        },
        nameRules: [
            v => !!v || 'Name is required',
            v => (v && v.length >= 3) || 'Name must be more than 3 characters',
            v => (v && v.length <= 100) || 'Name must be less than 100 characters'
        ],
        linkRules: [
            v => !!v || 'Link is required',
            v => (v && v.length >= 3) || 'Link must be more than 3 characters',
            v => (v && v.length <= 100) || 'Link must be less than 100 characters'
        ],
        typeRules: [v => (v && v.length >= 1) || 'Type is required']
    }),
    components: {
        'add-asset-template': AddAssetTemplate
    },
    computed: {
        ...mapState('assetInteraction', ['assetSelected', 'drawnShape'])
    },
    methods: {
        ...mapActions('printers', ['postPrinter']),
        ...mapMutations('assetInteraction', ['setViewingMode']),
        save() {
            if (this.$refs.form.validate()) {
                this.printer.floorId = this.floorId
                this.printer.shape = JSON.stringify(this.drawnShape)
                this.postPrinter(this.printer).then(() => {
                    this.setViewingMode()
                })
            }
        }
    }
}
</script>

