<template>
    <v-layout class="container-fluid">
        <div id="map" class="main-map"></div>
        <MeetingRoomDetails v-if="meetingRoomDetails && mapStates.viewing" />
        <StartDrawing v-if="mapStates.drawing" />
        <component v-bind:is="addAssetComponent" v-bind:floorId="floorId"></component>
    </v-layout>
</template>

<script>
import { mapService } from '@/services/map.service.js'
import { meetingRoom, PRINTER, BATHROOM, KITCHEN, DESK } from '@/store/commonSpaces.entityTypes.js'
import { mapActions, mapState, mapMutations } from 'vuex'
import MeetingRoomDetails from './MeetingRoomDetails.vue'
import StartDrawing from './StartDrawing.vue'
import AddMeetingRoom from './add-asset/AddMeetingRoom.vue'
import AddKitchen from './add-asset/AddKitchen.vue'
import AddPrinter from './add-asset/AddPrinter.vue'
import AddBathroom from './add-asset/AddBathroom.vue'
import AddDesk from './add-asset/AddDesk.vue'
import * as fromService from '@/services'
import store from '@/store'

export default {
    name: 'MapLayout',
    props: {
        imageContent: { required: true },
        floorId: { required: true }
    },
    components: {
        MeetingRoomDetails: MeetingRoomDetails,
        StartDrawing: StartDrawing,
        'add-meetingroom': AddMeetingRoom,
        'add-kitchen': AddKitchen,
        'add-printer': AddPrinter,
        'add-bathroom': AddBathroom,
        'add-desk': AddDesk
    },
    computed: {
        ...mapState('meetingRooms', ['meetingRoomDetails']),
        ...mapState('assetInteraction', ['assetSelected', 'mapStates', 'reloadMap']),
        addAssetComponent() {
            if (this.mapStates.adding || this.mapStates.editing) {
                return 'add-' + this.assetSelected.name.toLowerCase().replace(' ', '')
            }
            return null
        }
    },
    data: () => ({
        map: undefined,
        layer: undefined,
        layersList: [],
        commonSpacesService: null,
        select: null,
        draw: undefined,
        snap: undefined,
        modify: undefined,
        translate: undefined
    }),
    mounted() {
        // create map based on layoutImage
        this.map = mapService.createMap(this.imageContent)
        this.commonSpacesService = new fromService.CommonSpacesService(store.getter['account/token'])
        // get common spaces
        this.getCommonSpacesLayers()
        //add select interaction
        this.createSelectInteraction()
        this.addSelectInteraction()
    },
    watch: {
        mapStates(value) {
            let state = Object.entries(value).filter(e => e[1] === true)
            let activeState = state[0] || state[0][0]
            if (activeState) {
                //based on the state we change the interactions added on the map
                this.removeAllInteractions()
                switch (activeState[0]) {
                    case 'drawing':
                        this.addDrawInteraction()
                        break
                    case 'adding':
                        break
                    case 'editing':
                        break
                    default:
                        this.addSelectInteraction()
                }
            }
        },
        reloadMap() {
            if (this.reloadMap) {
                // remove all layers
                this.map = mapService.removeAllLayers(this.map)
                this.layersList = []
                // add the image layer
                this.map.addLayer(mapService.createImageLayer(this.imageContent))
                // get common spaces
                this.getCommonSpacesLayers()
                // set the variable reload map from state to false
                this.setReloadMap(false)
            }
        }
    },
    methods: {
        ...mapActions('meetingRooms', ['getMeetingRoomDetails']),
        ...mapActions('assetInteraction', ['setDrawnShape', 'setReloadMap']),
        ...mapMutations('assetInteraction', ['setViewingMode', 'setDrawingMode', 'setAddingMode', 'setEditingMode']),
        getCommonSpacesLayers() {
            this.commonSpacesService.getCommonSpaces(this.floorId).then(commonSpaceDictionary => {
                for (var commonSpaceKey in commonSpaceDictionary) {
                    var featureCollection = JSON.parse(commonSpaceDictionary[commonSpaceKey])
                    this.addLayerWithFeatures(featureCollection, commonSpaceKey)
                }
            })
        },
        addLayerWithFeatures(featureCollection, type) {
            // create layer based on feature type
            switch (type) {
                case 'printer':
                    this.layer = mapService.createVectorLayerWithFeatures(PRINTER, featureCollection)
                    break
                case 'bathroom':
                    this.layer = mapService.createVectorLayerWithFeatures(BATHROOM, featureCollection)
                    break
                case 'meetingroom':
                    this.layer = mapService.createVectorLayerWithFeatures(meetingRoom, featureCollection)
                    break
                case 'kitchen':
                    this.layer = mapService.createVectorLayerWithFeatures(KITCHEN, featureCollection)
                    break
                case 'desk':
                    this.layer = mapService.createVectorLayerWithFeatures(DESK, featureCollection)
                    break
            }
            // add the layer to map
            this.map.addLayer(this.layer)
            // add the layer to layersList
            this.layersList.push(this.layer)
        },
        // function that checks if there is a layer with a specified name in layersList
        checkLayersList: function() {
            var name = this.assetSelected.name
            // check (based on the name) if the layer is already in the list
            var searchedLayer = this.layersList.find(layer => {
                return layer.get('name') === name
            })
            // if there is use it
            if (searchedLayer) {
                this.layer = searchedLayer
            } else {
                // if not create one
                this.addLayer()
            }
        },
        // function that adds a new Layer
        addLayer: function() {
            // create the layer based on the propreties of the object selected in treeview
            this.layer = mapService.createVectorLayer(this.assetSelected)
            // add the layer to map
            this.map.addLayer(this.layer)
            // add the layer to layersList
            this.layersList.push(this.layer)
        },
        // add create function (separately from add function) just for the interactions targetting the map not the layers
        createSelectInteraction() {
            // create select interacton
            this.select = mapService.selectShape()

            // get selected feature id
            this.select.on('select', e => {
                var selectedFeatureId = null
                if (e.target.getFeatures().getArray()[0]) {
                    selectedFeatureId = e.target
                        .getFeatures()
                        .getArray()[0]
                        .getProperties().Id
                }
                this.getMeetingRoomDetails(selectedFeatureId)
            })
        },
        removeAllInteractions() {
            this.map.removeInteraction(this.select)
            this.map.removeInteraction(this.draw)
            this.map.removeInteraction(this.snap)
            this.map.removeInteraction(this.modify)
            this.map.removeInteraction(this.translate)
        },
        addSelectInteraction() {
            // add select interaction to map
            this.map.addInteraction(this.select)
        },
        addDrawInteraction: function() {
            this.checkLayersList()
            // choose what to draw based on the drawing style of the asset
            switch (this.assetSelected.type) {
                case 'Point':
                    this.draw = mapService.drawRectangle(this.layer, this.assetSelected)
                    break
                default:
                    this.draw = mapService.drawPolygon(this.layer, this.assetSelected)
            }
            // what to do when the draw wnds
            this.draw.on('drawend', e => {
                // get the feature just drawn
                var finishedDraw = mapService.getGeoJsonFromFeature(e.feature)
                // save it to state
                this.setDrawnShape(finishedDraw)
                // change the state to adding
                this.setAddingMode()
            })
            this.map.addInteraction(this.draw)
        }
    }
}
</script>

<style lang="scss" scoped>
@import '@/styles/global.scss';

.mapLayout {
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}
</style>
