<template>
  <div>
    <add-asset-template>
      <template v-slot:form>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field v-model="meetingRoom.name" :rules="nameRules" label="Name" required></v-text-field>
          <v-text-field v-model="meetingRoom.code" :rules="codeRules" label="Code" required></v-text-field>
          <v-text-field
            v-model="meetingRoom.numberOfPersons"
            :rules="capacityRules"
            label="Capacity"
            required
          ></v-text-field>
        </v-form>
      </template>
      <template v-slot:buttons>
        <v-btn :disabled="!valid" color="primary" @click="save">Save</v-btn>
      </template>
    </add-asset-template>
  </div>
</template>

<script>
import AddAssetTemplate from './AddAssetTemplate.vue'
import { mapState, mapActions, mapMutations } from 'vuex'

export default {
    props: {
        floorId: {
            type: String
        }
    },
    data: () => ({
        valid: true,
        meetingRoom: {
            name: '',
            code: '',
            numberOfPersons: null,
            floorId: undefined,
            shape: undefined
        },
        nameRules: [
            v => !!v || 'Name is required',
            v => (v && v.length >= 3) || 'Name must be more than 3 characters',
            v => (v && v.length <= 100) || 'Name must be less than 100 characters'
        ],
        codeRules: [
            v => !!v || 'Code is required',
            v => (v && v.length >= 3) || 'Code must be more than 3 characters',
            v => (v && v.length <= 100) || 'Name must be less than 100 characters'
        ],
        capacityRules: [
            v => !!v || 'Capacity is required',
            v => (v && v >= 3) || 'Capacity must be more than 2 persons',
            v => (v && v <= 100) || 'Capacity must be less than 100 persons'
        ]
    }),
    components: {
        'add-asset-template': AddAssetTemplate
    },
    computed: {
        ...mapState('assetInteraction', ['assetSelected', 'drawnShape'])
    },
    methods: {
        ...mapActions('meetingRooms', ['postMeetingRoom']),
        ...mapMutations('assetInteraction', ['setViewingMode']),
        save() {
            if (this.$refs.form.validate()) {
                this.meetingRoom.floorId = this.floorId
                this.meetingRoom.shape = JSON.stringify(this.drawnShape)
                this.postMeetingRoom(this.meetingRoom).then(() => {
                    this.setViewingMode()
                })
            }
        }
    }
}
</script>

